      templates: null,
    };
  },
  mounted() {},
  methods: {
    async getTemplates() {
      let { default: Templates } = await import("../templates");
      this.templates.push(Templates.templates);
      Templates = null;
    },
    destroyTemplates() {
      this.templates = null;
    },
  },
};
</script>

